export var select = function (recordKey) {
    if (typeof recordKey === 'function') {
        return recordKey;
    }
    return function (record, recordIndex) {
        if (typeof recordKey === 'undefined') {
            return recordIndex + '';
        }
        var parts = recordKey.split('.');
        var tmp = record;
        while (tmp && parts.length) {
            tmp = tmp[parts.shift()];
        }
        var t = typeof tmp;
        if (t === 'string') {
            return tmp;
        }
        else if (t === 'number') {
            return t + '';
        }
        return recordIndex + '';
    };
};
